import React from 'react';
import Gallery from '../../components/Gallery/Gallery';
import {imageForGalleryType} from "../../types/types";

type PropsType = {
    images: Array<imageForGalleryType>
}

const images: Array<imageForGalleryType> = [
    {
        original: 'https://avatarko.ru/img/kartinka/33/luna_pisma_zhivotnye_letuchaya_mysh_33408.jpg',
        thumbnail: 'https://vps.ua/blog/wp-content/uploads/2017/09/ddos-250x150.jpg'
    },
    {
        original: 'https://ktovkurse.com/wp-content/uploads/2017/03/oil_price_drop_01_466461452-1000x600.jpg',
        thumbnail: 'data:image/jpeg;base64,'
    }
];

class GalleryContainer extends React.Component<PropsType> {
    componentDidMount(): void {
        document.title = 'Галерея'
    }

    render() {
        return <Gallery images={images} />;
    }
}

export default GalleryContainer;